.App {
  text-align: center;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  padding: 5px 0;
  justify-content: space-around;
  
  
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-header button {
  width: 30%;
  padding: 2px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 22px;
}

.App-link {
  color: #61dafb;
}
.client {
  display: flex;
  justify-content: space-between;
}
.header {
  padding: 10px 0;  
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  background-color: #ccc;
}
.client span {
  display: inline-block;
  width: 25%;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
